import React, { useState, useEffect } from "react";

function MouseLocation() {
    const [location, setLocation] = useState({ clientX: 0, clientY: 0 });
    const mouseLocation = (e) => {
        setLocation({ clientX: e.clientX, clientY: e.clientY });
    };
    useEffect(() => {
        console.log("Mouse component is Mount");
        document.addEventListener('mousemove', mouseLocation);
        return () => {
            console.log("Mouse component is unmount")
            document.removeEventListener('mousemove', mouseLocation);
        };
    }, []);
    return (
        <>
            <hr className="position-fixed hr1" style={{ top: location.clientY - 11 + "px", left: location.clientX - 11 + "px" }} />
            {/* <hr className="position-fixed hr2" style={{ left: clientX + "px", height: clientY + "px" }} /> */}
        </>
    );
};
export default MouseLocation;