import React, { Suspense, lazy } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Header from "./Component/Header/Header";
import MouseLocation from './Component/MouseLocation/MouseLocation';
const CounterPage = lazy(() => import("./Pages/Counter/CounterPage"));
const Posts = lazy(() => import("./Pages/Fake Posts/Posts"));
const GraphqlPostsExample = lazy(() => import("./Pages/GraphQlDemoComponent/GraphQlPostsExample"));

export default function Routes() {
    return (
        <Suspense fallback={<div className="text-center mt-5 pt-3">Loading....</div>}>
            <MouseLocation />
            <Header />
            <Switch>
                <Route exact path="/counter" component={CounterPage} />
                <Route exact path="/posts" component={Posts} />
                <Route exact path="/graphqlposts" component={GraphqlPostsExample} />
                <Redirect to="/counter" from="*" />
            </Switch>
        </Suspense>
    );
};