import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import { Navbar, NavbarBrand, NavbarToggler, Collapse, Nav } from "reactstrap";

const Header = (props) => {

    const [collapsed, setCollapsed] = useState(true);
    const toggleNavbar = () => setCollapsed(!collapsed);

    useEffect(() => {
        console.log("Header component is Mount");
    }, []);

    return (
        <div className="header" >
            <Navbar color="faded" light expand="md">
                <NavbarBrand>React App</NavbarBrand>
                <NavbarToggler size="sm" onClick={toggleNavbar} className="ml-auto" />
                <Collapse isOpen={!collapsed} navbar>
                    <Nav navbar className="ml-auto">
                        <NavLink className="headerLink" onClick={() => setCollapsed(true)} activeClassName="isActive" to="/counter">Counter</NavLink>
                        <NavLink className="headerLink" onClick={() => setCollapsed(true)} activeClassName="isActive" to="/posts">Posts</NavLink>
                        <NavLink className="headerLink" onClick={() => setCollapsed(true)} activeClassName="isActive" to="/graphqlposts">Graphql Posts</NavLink>
                    </Nav>
                </Collapse>
            </Navbar>
        </div>
    );
};
export default Header;